<template>
  <a-modal
    :title="title"
    :width="640"
    :visible="visible"
    :confirmLoading="loading"
    @ok="
      () => {
        $emit('ok')
      }
    "
    @cancel="
      () => {
        $emit('cancel')
      }
    "
  >
    <a-spin :spinning="loading">
      <a-form-model ref="bindForm" :model="model" :rules="rules" v-bind="formItemLayout">
        <a-form-model-item label="推流方式" prop="rtp_proto">
          <a-select placeholder="请选择" v-model="model.rtp_proto">
            <a-select-option value="TCP">TCP主动</a-select-option>
            <a-select-option value="UDP">UDP主动</a-select-option>
          </a-select>
        </a-form-model-item>
      </a-form-model>
    </a-spin>
  </a-modal>
</template>

<script>

const defaultModel = {
    rtp_proto: 'TCP'
}
export default {
    props: {
        visible: {
            type: Boolean,
            default: true
        },
        loading: {
            type: Boolean,
            default: () => false
        }
    },
    data () {
        return {
            title: '设置推流方式',
            model: defaultModel,
            nvrItems: {},
            formItemLayout: {
                labelCol: { span: 6 },
                wrapperCol: { span: 14 }
            },
            rules: {
                rtp_proto: [{ required: true, message: '推流方式必须选择', trigger: 'blur' }]
            }
        }
    },
    methods: {
        resetForm () {
            this.model.rtp_proto = defaultModel.rtp_proto
        }
    }
}
</script>
<style lang="less" scoped>
.ant-select {
    min-width: 220px;
}
</style>
